<template>
  <div>
    <router-link tag="div" to="/" class="header-abs" v-show="showAbs">
      <span class="iconfont header-abs-back">&#xe616;</span>
    </router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      景点详情
      <router-link to='/'>
        <span class="iconfont header-fixed-back">&#xe616;</span>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "DetailHeader",
  data(){
    return {
      showAbs: true,
      opacityStyle:0
    }
  },
  methods:{
    handleScroll(){
      const top = document.documentElement.scrollTop;
      console.log(top)
      if(top > 60 ){
        let opacity = top / 140;
        opacity = opacity > 1 ? 1:opacity;
        this.opacityStyle = {
          opacity
        }
        this.showAbs = false;
      }else{
        this.showAbs = true;
      }
    }
  },
  activated(){
    window.addEventListener('scroll',this.handleScroll)
  },
  deactivated(){
    window.removeEventListener('scroll',this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/variables.styl'
.header-abs
  position: absolute
  left: .2rem
  top: .2rem
  width: .8rem
  height: .8rem
  border-radius: .4rem
  background: rgba(0, 0, 0, .8)
  line-height: .8rem
  text-align: center
  .header-abs-back
    color: #fff
    font-size: .4rem
.header-fixed
  position: fixed
  z-index: 2
  top: 0
  left: 0
  right: 0
  height: $headerHeight
  line-height: $headerHeight
  text-align: center
  color: #fff
  background: $bgColor
  font-size: .32rem
  .header-fixed-back
    position: absolute
    width: .64rem
    text-align: center
    color: #fff
    font-size: .4rem
    top: 0
    left: 0
</style>



